import React from "react";

export default ({data}) => (
    <div>
        <h1>{data.site.siteMetadata.author}</h1>
        <p>
            <a href={data.site.siteMetadata.email}>{data.site.siteMetadata.email}</a>
        </p>
    </div>
);

export const query = graphql`
  query ContactQuery {
    site {
      siteMetadata {
        title
        author
        email
      }
    }
  }
`


